import React, { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import { graphql } from 'gatsby';
import nodeFetch from 'node-fetch';
import ReactGA from 'react-ga';
import loadable from '@loadable/component';
import Layout from '../components/Layout';
import OpeningAnimation from '../components/OpeningAnimation';
// 読み込み速度向上のためファーストビュー以外は loadable を使って import する
import NavbarSection from '../components/sections/Navbar';
import MainvisualSection from '../components/sections/Mainvisual';
import defaultData from '../components/defaultData';

interface IndexPageProps {
  data: {
    site: {
      siteMetadata: {
        lang: string;
        locale: string;
        title: string;
        siteName: string;
        description: string;
        siteUrl: string;
        ogpUrl: string;
      };
    };
  };
}

const Index: React.FC<IndexPageProps> = props => {
  const { siteMetadata } = props.data.site;

  const AboutSection = loadable(() => import('../components/sections/About'));
  const ServiceSection = loadable(
    () => import('../components/sections/Service')
  );
  const CompanySection = loadable(
    () => import('../components/sections/Company')
  );
  const ContactSection = loadable(
    () => import('../components/sections/Contact')
  );
  const FooterSection = loadable(() => import('../components/sections/Footer'));

  const [aboutData, setAboutData] = useState([
    {
      title: '',
      description: ''
    }
  ]);
  const [serviceData, setServiceData] = useState([]);
  const [companyData, setCompanyData] = useState([]);

  const googleSheetsResponseToObject = (res: any) => {
    const columns: string[] = res.values.shift();

    const datas = res.values.map((fields: any[]) => {
      const data = fields.reduce((obj: any, val: any, i: number) => {
        const key = columns[i];
        obj[key] = val;
        return obj;
      }, {});

      return data;
    });

    return datas;
  };

  const fetch = async (sheetName: string, range: string) => {
    try {
      const endpoint: string = 'https://sheets.googleapis.com/v4/spreadsheets/';
      const spreadsheetId: string =
        '17m-7BWL_46Cy6jsHE-VKJmmJQdHMrbu_Hdh7q3g43l8';
      const apiKey: string = 'AIzaSyCyflg5eN9uMwbBUbhugW7KbXwbmM93en8';
      const resource: string = `/values/${sheetName}${range}?key=${apiKey}`;
      const apiUrl: string = endpoint + spreadsheetId + resource;

      const res: any = await nodeFetch(apiUrl);

      if (!res || !res.ok) throw new Error();

      const data: any = await res.json();

      return googleSheetsResponseToObject(data);
    } catch (e: any) {
      return defaultData.sections[sheetName].contents;
    }
  };

  useEffect(() => {
    ReactGA.initialize('UA-189752134-1');
    ReactGA.pageview(window.location.pathname + window.location.search);

    const fetchData: any = async () => {
      setAboutData(await fetch('about', '!A1:B'));
      setServiceData(await fetch('service', '!A1:C'));
      setCompanyData(await fetch('company', '!A1:C'));
    };

    fetchData();
  }, []);

  return (
    <Layout>
      <Helmet>
        <html lang={siteMetadata.lang} />
        <meta name='description' content={siteMetadata.description} />
        <title>{siteMetadata.title}</title>
        <link rel='canonical' href={siteMetadata.siteUrl} />
        <meta property='og:title' content={siteMetadata.title} />
        <meta property='og:type' content='website' />
        <meta property='og:url' content={siteMetadata.siteUrl} />
        <meta property='og:description' content={siteMetadata.description} />
        <meta property='og:locale' content={siteMetadata.locale} />
        <meta property='og:site_name' content={siteMetadata.siteName} />
        <meta property='og:image' content={siteMetadata.ogpUrl} />
        <meta property='og:image:secure_url' content={siteMetadata.ogpUrl} />
        <meta property='og:image:type' content='image/png' />
        <meta property='og:image:width' content='1200' />
        <meta property='og:image:height' content='630' />
        <meta property='og:image:alt' content={siteMetadata.siteName} />
        <meta name='twitter:card' content='summary_large_image' />
        <meta name='twitter:title' content={siteMetadata.title} />
        <meta name='twitter:description' content={siteMetadata.description} />
        <meta name='twitter:image' content={siteMetadata.ogpUrl} />
      </Helmet>
      <MainvisualSection />
      <NavbarSection />
      <AboutSection data={aboutData} />
      <ServiceSection data={serviceData} />
      <CompanySection data={companyData} />
      <ContactSection />
      <FooterSection />
    </Layout>
  );
};

export const pageQuery = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        lang
        locale
        title
        siteName
        description
        siteUrl
        ogpUrl
      }
    }
  }
`;

export default Index;
